.easy-editor-toolbar {
  position: relative;
  padding: 8px;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
  transition: padding 600ms cubic-bezier(0.19, 1, 0.22, 1);
  display: flex;
  align-items: center;
  height: 40px;
  box-sizing: border-box;
}
.easy-editor-toolbar__btn {
  display: inline-block;
  width: 24px;
  height: 24px;
  border: none;
  outline: none;
  border-radius: 4px;
  background-color: var(--tide-background-color, #fff);
  font-size: 16px;
  color: inherit;
  padding: 4px;
  cursor: pointer;
}
.easy-editor-toolbar__item {
  display: flex;
}
.easy-editor-toolbar__item:not(:first-child) {
  margin-left: 8px;
}